import controller from "../controllers/course.controller";
import { Router } from "express";
import auth from "../middleware/auth";
const router = Router();

router.get("/", controller.findAll);
router.put("/updateIndex", controller.updateIndex);
router.get("/:id", controller.findOne);
router.post("/", controller.create);

router.delete("/:id", controller.remove);

router.put("/form6", controller.updateForm6);
router.post("/form6", controller.createForm6);
router.get("/form6/:id", controller.findOneForm6);
router.put("/:id", controller.update);
export default router;
